import React from 'react';
import styles from "./Users.module.css";
import usersAvatar from "../../assets/images/user.jpg";
import { NavLink } from 'react-router-dom';

let Users = (props) => {

  let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
  
  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }
  let pl = props.currentPage - 5 <= 0 ? 0 : props.currentPage - 5
  let pr = props.currentPage + 5
  let slicedPages = pages.slice(pl, pr)

  const prevClick = (currentPage) => {
    let count = currentPage - 10 <= 0 ? 1 : currentPage - 10
    props.setCurrentPage(count)
  }

  const nextClick = (currentPage) => {
    let count = currentPage + 10 > props.totalUsersCount ? 1 : currentPage + 10
    props.setCurrentPage(count)
  }

  return <div>
    <div>
      {
        props.currentPage === 1
        ? null
        : <button onClick={() => prevClick(props.currentPage)}>prev</button>
      }
      
      {slicedPages.map(p => {
        return <span key={p} className={props.currentPage === p ? styles.selectedPage : ''}
          onClick={(e) => {
            props.onPageChanged(p);
          }}>{p}</span>
      })}
      {
        props.currentPage === pages
        ? null
        : <button onClick={() => nextClick(props.currentPage)}>next</button>
      }
    </div>
    {
      props.users.map(u => <div key={u.id}>
        <span>
          <div>
            <NavLink to={'/profile/' + u.id}>
              <img alt='' src={u.photos.small != null ? u.photos.small : usersAvatar}
              className={styles.userPhoto} />
            </NavLink>
          </div>
          <div>
            {u.followed
              ? <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                props.unfollow(u.id)
              }}>Unfollow</button>
              : <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                props.follow(u.id)
                }}>Follow</button>}
          </div>
        </span>
        <span>
          <span>
            <div>{u.name}</div>
            <div>{u.status}</div>
          </span>
          <span>
            {/* <div>{"u.location.country"}</div>
            <div>{"u.location.city"}</div> */}
          </span>
        </span>
      </div>)
    }
  </div>
}

export default Users;