import React from 'react';
import Profile from './Profile';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { setUserProfile, getStatus, getUserProfile, updateStatus } from '../../redux/profile-reducer';
import { withAuthNavigate } from '../../hoc/withAuthNavigate';
import { withRouter } from '../../hoc/withRouter';

class ProfileContainer extends React.Component {
  componentDidMount() {
    let userId = this.props.router.params.userId
    if (!userId) {
      userId = this.props.userId
    }
    this.props.getUserProfile(userId)
    this.props.getStatus(userId)
  }

  componentDidUpdate(prevProps) {
    let userId = this.props.router.params.userId
    if (prevProps.router.params.userId !== userId) {
      let userId = this.props.userId
      this.props.getUserProfile(userId)
    }
  }

  render() {
    return (
      <Profile {...this.props} status={this.props.status} updateStatus={this.props.updateStatus} />
    )
  }
}

let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  userId: state.auth.userId,
  isAuth: state.auth.isAuth,
})

let AuthNavigateComponent = compose(connect(mapStateToProps, { setUserProfile, getStatus, getUserProfile, updateStatus }), withRouter, 
  withAuthNavigate)(ProfileContainer)

export default AuthNavigateComponent