import { useFormik } from 'formik';
import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';

const MyPosts = React.memo(props => {
  let postsElements =
    [...props.posts].reverse().map(p => <Post key={p.id} message={p.message} likesCount={p.likesCount} />)

  const formik = useFormik({
    initialValues: {
      postText: ''
    },
    onSubmit: () => {
      props.addPost(formik.values.postText)
      formik.values.postText = ''
    }
  })

  return (
    <div className={s.postsBlock}>
      <h3>My posts</h3>
      <MyPostsForm formik={formik} />
      <div className={s.posts}>
        {postsElements}
      </div>
    </div>
  )
})

const MyPostsForm = (props) => {
  return (
    <form onSubmit={props.formik.handleSubmit}>
      <div>
        <input type="text" name='postText' onChange={props.formik.handleChange} />
      </div>
      <div>
        <button type='submit'>Add post</button>
      </div>
    </form>
  )
}

export default MyPosts;